import React, {useRef,useState,useEffect} from "react";
import {Question} from "./Question";
import ProgressAnswer from "./AnswerProgress";
import axios from "axios";
import '../../css/LearningLossModal.css';


export function Main({studyLevel,questions,setQuestions,indexActiveQuestion,setIndexActiveQuestion}) {
    const [answerProgress, setAnswerProgress] = useState(0);
    const [learningLossStatus, setLearningLossStatus] = useState(0);
    const [learningLossIsOpen,setLearingLossIsOpen] = useState(false)
    const modal = useRef();
    useEffect(() => {
        window.scrollTo({top:0,left:0})
    }, [indexActiveQuestion]);
    const [answers, setAnswers] = useState(new Map());

    useEffect(() => {
        setAnswerProgress(countAnswerProgress());
        return()=>{
            setAnswerProgress(0);
        }
    }, [answers]);


    const analyticUrl = 'https://learningloss.marchgis.com/api';
    const studyLevelPath = new Map();
    studyLevelPath.set("mi",'/predicted_cluster/mi');
    studyLevelPath.set("mts",'/predicted_cluster/mts');
    studyLevelPath.set("ma",'/predicted_cluster/ma');

    function countAnswerProgress() {
        let totalQuestionNumber = 0;
        questions.map((question) => {
            totalQuestionNumber = totalQuestionNumber + question.questions.length;
        });

        let progress = 0;
        for (let [key, value] of answers) {
            progress=progress+value.length
        }

        return (Math.round((progress / totalQuestionNumber) * 100));
    }
    function rewind(){
        const newIndex = indexActiveQuestion-1;
        if(newIndex>=0){
            setIndexActiveQuestion( newIndex )
        }
    }
    function forward(){
        const newIndex = indexActiveQuestion+1;
        if(questions.length-1>=newIndex){
            setIndexActiveQuestion( newIndex )
        }
    }
    function prepareSubmit(){
        let answerParams = '?';
        for (let [key, answer] of answers) {
            let tAnswers = answer;
            tAnswers.forEach((tA,tAIndex)=>{
                questions.map((question)=>{
                    const tQuestions = question.questions;
                    tQuestions.forEach((tQ,tQIndex)=>{
                        if(tQ.no == tA.noSoal){
                            const tOptions = tQ.options;
                            tOptions.forEach((tO,tOIndex)=>{
                                if(tO === tA.data.answer){
                                    if(answerParams==='?'){
                                        answerParams=answerParams+tQ.questionCode+'='+(tOIndex+1);
                                    }else{
                                        answerParams=answerParams+'&'+tQ.questionCode+'='+(tOIndex+1);
                                    }
                                }
                            });
                        }
                    });
                });
            });
        }
        return answerParams;
    }
    function submit(){
        console.log('submit')
        const answersPath = prepareSubmit();
        axios.post(analyticUrl+studyLevelPath.get(studyLevel)+answersPath).then((response) => {
            setLearningLossStatus(response.data.prediction[0]);
            setLearingLossIsOpen(true)
        });
    }
    useEffect(()=>{
        if(modal.current!==null && learningLossIsOpen){
            // modal.current.style.backgroundImage='.   notLL.png';
            modal.current.style.display='block';
            if(learningLossStatus==0){
                modal.current.children[0].style.backgroundImage= 'url(images/notLL.png)';
            }else{
                modal.current.children[0].style.backgroundImage= 'url(images/LL.png)';
            }

            modal.current.children[0].style.backgroundRepeat= 'no-repeat';
            modal.current.children[0].style.backgroundSize= '100% 470px';
        }
    },[learningLossIsOpen]);

    let formQuestions =(<p>Not loaded yet</p>);
    if(questions.length>0){
        formQuestions=(
            <Question questions={questions[indexActiveQuestion]} indexActiveQuestion={indexActiveQuestion} setAnswers={setAnswers} answers={answers}/>
        );
    }
    let modalResult = '';
    if(learningLossIsOpen){
        modalResult = (<div ref={modal}  className={'modal'}>
            <div  className={'modal-inner'}>
                <div  className={'modal-main'}>
                    <div className={'congrats'}>
                    </div>

                </div>
                <div className={'modal-footer-result'}>
                    <div onClick={()=>{setLearingLossIsOpen(false)}} className={'modal-footer-next'}>
                        Lanjutkan
                        <i className="fa fa-arrow-right u-ml5"></i>
                    </div>
                </div>
            </div>
        </div>)
    }

    return (
        <div className="question-main-wrapper">
                <div className="question-wrapper">
                {formQuestions}
            </div>
            <div className='question-nav-wrapper'>
                <div className='question-back'>
                    <span className='question-btn back' onClick={() => {
                        rewind()
                    }}>Kembali</span>
                </div>
                <div className='answer-progress'>
                    <ProgressAnswer percentage={answerProgress}/>
                </div>
                <div className='question-next'>
                    {(answerProgress===100&&indexActiveQuestion===questions.length-1)?(
                        <span className='question-btn submit' onClick={() => {
                            submit()
                        }}>Submit</span>
                    ):(
                        <span className='question-btn next' onClick={() => {
                            forward()
                        }}>Selanjutnya</span>
                    )}
                </div>
                {/*<LearningLossTrue isOpen={le} result={learningLossStatus}/>*/}
                {modalResult}
            </div>

        </div>
    );
}
