import React, {useState,useEffect} from "react";
import Header from "./Header";
import Footer from "./Footer";
import {Main} from "./Main";
import "../../css/style-options.css";
import {DataHolderQuestioner} from "../../Data/DataHolder";
import {useParams} from "react-router-dom"


function App(){
    const[questionsStore, setQuestions] = useState([])
    const [indexActiveQuestion,setIndexActiveQuestion] = useState(0);
    const params = useParams();
    useEffect(()=>{
        const tempQuestions = DataHolderQuestioner(params.jenjang);
        if(tempQuestions!==undefined){
            setQuestions(tempQuestions);
        }else{
            setQuestions([]);
        }


    },[])

    return (

      <div>
        <Header questions={questionsStore} indextActiveQuestion={indexActiveQuestion} setIndexActiveQuestion={setIndexActiveQuestion}/>
         <Main studyLevel={params.jenjang} questions={questionsStore} setQuestions={setQuestions} indexActiveQuestion={indexActiveQuestion} setIndexActiveQuestion={setIndexActiveQuestion} />
        <Footer/>
      </div>
    );


}

export default App;
