import '../css/HomeTop.css';
import {Link} from "react-router-dom";
import {useEffect, useRef, useState} from "react";
import {getLearningLossDescription} from "../Data/DataHolder";

function HomeTop() {
    const [isOpen, setIsOpen] = useState(false);
    const [descriptionLearningLossIsOpen, setDescriptionLearningLossIsOpen] = useState(false)

    const domPilihJenjang = useRef();
    const domDescriptionLearningLoss = useRef();
    useEffect(()=> {
        if (domPilihJenjang.current !== null && isOpen) {
            domPilihJenjang.current.style.display = "block";
        }
    },[isOpen]);

    useEffect(()=>{
        if(domDescriptionLearningLoss.current !== null && descriptionLearningLossIsOpen){
            domDescriptionLearningLoss.current.style.display = "block";
        }
    },[descriptionLearningLossIsOpen]);

    const ModalPilihJenjang = ()=>{
        if(isOpen){
            return (<div ref={domPilihJenjang} className={'modal'}>
                <div className={'modal-pilih-jenjang-inner'}>
                    <div className={'header-pilih-jenjang'}></div>
                    <div className={'body-pilih-jenjang'}>
                        <p>Silahkan pilih jenjang</p>
                        <Link className={'btn-jenjang'} to={'/prediksi/mi'}><i className="fa-solid fa-child"></i> &nbsp; Madrasah Ibtidaiyah </Link>
                        <Link className={'btn-jenjang'} to={'/prediksi/mts'}><i className="fa-solid fa-child-reaching"></i>&nbsp;Madrasah Tsanawiyah</Link>
                        <Link className={'btn-jenjang'} to={'/prediksi/ma'}><i className="fa-solid fa-user"></i>&nbsp;Madrasah Aliyah</Link>
                        <a className={'btn-jenjang'} onClick={()=>{setIsOpen(false)}}><i
                            className="fa-solid fa-right-from-bracket"></i>&nbsp;Tutup</a>
                    </div>
                </div>

            </div>)
        }
        return '';
    }

    const ModalDescriptionLearningLoss= ()=>{
        if(descriptionLearningLossIsOpen){
            return (<div ref={domDescriptionLearningLoss} className={'modal'}>
                <div className={'modal-description-learning-loss'}>
                    <div className={'modal-desc-header'}></div>
                    <div className={'modal-description'}>
                        <h2>Learning Loss</h2>
                        <p><b>Learning loss</b> adalah sebuah kondisi terjadinya kehilangan pengetahuan dan keterampilan baik secara umum maupun spesifik karena proses akademik yang mengalami kemunduran.  Learning loss disebabkan oleh kondisi seperti periode libur panjang, putus sekolah peserta didik, dan ditutupnya sekolah tatap muka karena kondisi kritis atau mendesak seperti pandemi. Learning loss diberbagai bidang pendidikan akibat pandemi COVID-19 menjadi pesoalan pendidikan secara global.</p>
                        <p>Kementerian Agama, melalui Direktorat Jenderal Pendidikan Islam, memandang penting mengatasi learning loss yang terjadi pada dunia pendidikan, khususnya di Madrasah, sebagai dampak pandemi COVID-19</p>
                    </div>
                    <button onClick={()=>{setDescriptionLearningLossIsOpen(false)}} className='modal-close-btn'>Tutup</button>
                </div>
            </div>);
        }
        return '';
    }


    return (
        <>
            <ModalPilihJenjang/>
            <ModalDescriptionLearningLoss/>
            <div className='top-wrapper'></div>
            <div className='top-container'>
                <h1>Deteksi dan Mitigasi</h1>
                <h1>Learning Loss</h1>
                <p>Deteksi dan Mitigasi Learning loss pada Pendidikan Agama Islam di madrasah dengan pedekatan Sains Data </p>
                <a onClick={()=>{setDescriptionLearningLossIsOpen(true)}} className={'btn-home'}>Apa itu Learning Loss&nbsp;<i className="fa-solid fa-circle-arrow-right"></i></a>
                <a onClick={()=>{setIsOpen(true)}} className={'btn-home'}>Mulai Prediksi&nbsp;<i className="fa-solid fa-circle-arrow-right"></i></a>
            </div>
        </>
    );
}

export default HomeTop;